/* ==========================================================================
   Mobile header responsive style - begin
   ========================================================================== */

@include ipad-landscape {
	
	.eltdf-mobile-header {
		display: block;
	}
}

@include phone-landscape {

	.eltdf-mobile-header {

		.eltdf-mobile-nav {

			max-height: 250px;
			overflow-y: auto;
		}
	}
}

@include phone-portrait {

	.eltdf-mobile-header {

		.eltdf-mobile-nav {

			max-height: 460px;
			overflow-y: auto;
		}
	}
}
/* ==========================================================================
   Mobile header responsive style - end
   ========================================================================== */